*{
  padding: 0;
  margin: 0;
  background-color: black;
  color: white;
  overflow: hidden;
}

.left{
  background-color: blue;
  position: absolute;
  width: 7.5%;
  height: 100%;
}

.right{
  background-color: red;
  position: absolute;
  right: 0;
  width: 7.5%;
  height: 100%;
}

.loading{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  text-align: center;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.5);
}

.loading img{
  width: 250px;
  height: 250px;
  background: none;
}